import React, { Component } from 'react';
import './App.css';
import styled from 'styled-components';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTwitter, faLinkedin, faGithub } from '@fortawesome/free-brands-svg-icons';

class App extends Component {
  render() {
    return (
      <div className="App">
        <AppHeader>
          {/* <img src="/profile-pic.jpg" className="App-logo" alt="logo" /> */}
          <Pic></Pic>
        </AppHeader>
        <Title>
          <h1>Tercio de Melo</h1>
          <div>
            <span>Software Engineer at </span>
            <a href="https://www.vtex.com/" target="_blank"><VTEX/></a>
          </div>
        </Title>
        <Info>
          <div class="pb3">terciodemelo@gmail.com</div>
          <div>
            <Contact href="https://twitter.com/terciodemelo"><FontAwesomeIcon icon={faTwitter}></FontAwesomeIcon></Contact>
            <Contact href="https://github.com/terciodemelo"><FontAwesomeIcon icon={faGithub}></FontAwesomeIcon></Contact>
            <Contact href="https://www.linkedin.com/in/tercio-de-melo-0b714010a/"><FontAwesomeIcon icon={faLinkedin}></FontAwesomeIcon></Contact>
          </div>
        </Info>
      </div>
    );
  }
}

const Contact = styled.a.attrs({
  className: "contact link"
})`
  color: var(--primary);
  font-size: 2rem;
  &:not(:first-child) {
    margin-left: 10px;
  }
  &:hover {
    color: var(--secondary);
  }
`

const Pic = styled.img.attrs({
  className: "h5 w5 br-100 ba ba--black-10 pa1 bg-white dib",
  src: "/profile-pic.jpg",
  alt: "picture"
})`
  object-fit: cover;
  object-position: 0% 75%;
`

const Title = styled.div.attrs({
  className: "title"
})``

const VTEX = styled.img.attrs({
  className: "vtex v-mid",
  height: 25,
  src: "/vtex.png",
  alt: "VTEX"
})``

const AppHeader = styled.header.attrs({
  className: "h5 pa4 mb5 white"
})`
  background-color: var(--primary)
`

const Info = styled.div.attrs({
  className: "info pt3",
})``

export default App;



